* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}
h1 {
  width: 1300px;
  margin: auto;
  text-align: left;
  padding: 24px 4px;
}
.mainlistbut {
  background-color: rgb(240, 239, 239);
    text-decoration: none;
    color: rgb(70, 69, 69);
    padding: 12px 48px;
    display: inline-block;
    position: relative;
    border: 1px solid rgba(0,0,0,0.21);
    border-bottom: 3px solid rgba(0,0,0,0.21);
    border-radius: 20px;
    text-shadow: 0 1px 0 rgba(0,0,0,0.15);
    font-size: 14px;
    font-weight: bolder;
    min-width: 300px;
    cursor: pointer;
}
.mainlistbutbox{
  width: 310px;
  margin: 40px auto;
}
.boomwrap {
  max-width: 400px;
  height: 500px;
  border-radius: 15px;
  background-color: #e8e9e9;
  padding: 12px;
  position: absolute;
  top: 25%;
  left: 25%;
  z-index: 3;
}
.boom {
  width: 350px;
  margin: auto;
  border: 1px blue solid;
  text-align: center;
}

.main-bg {
  height: 300px;
  background-image: url("./img/main.png");
  background-position: center;
  background-size: 50%;
}
.main-ul {
  max-width: 1300px;
  margin: auto;
  justify-content: center;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
li {
  margin: 12px;
  font-weight: bold;
}
.productBox {
  margin: 8px;
}
.shopname {
  font-size: 12px;
  color: rgb(68, 67, 67);
  margin-bottom: 12px;
}
a {
  cursor: pointer;
}
a .f-icon {
  border: 3px red solid;
  position: absolute;
  bottom: 125px;
  right: 10px;
  font-size: 25px;
}
a .icon {
  position: absolute;
  bottom: 125px;
  right: 10px;
  font-size: 25px;
}
span {
  color: rgb(232, 31, 31);
  font-weight: bolder;
  font-size: 18px;
}
div .coupon {
  background-image: url("./img/coupon.jpg");
  margin: auto;
  height: 200px;
  width: 200px;
  background-position: center;
  background-size: 100%;
}
.click {
  background-color: yellow;
}
.none {
  background-color: #f7f8fa;
}

/* PPL Css */
.pplbox {
  max-width: 1300px;
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
}
.ppl1box {
  width: 610px;
  margin: 28px 4px;
}
.ppl1 {
  width: 100%;
  position: relative;
}
.ppl2 {
  width: 100%;
  position: relative;
  height: 300px;
}
/* detail Css*/
