/* list Css */
.listprice{
  color: rgb(225, 32, 32);
}
.wrap {
  list-style: none;
  box-sizing: border-box;
}
.listWrap {
  max-width: 1300px;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.list {
  margin: 12px;
  padding: 4px;
  position: relative;
  border: 1px rgb(199, 201, 204) solid;
  border-radius:1px;
}
.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}
.img:hover {
  transform: scale(1.1);
}
.listcontent{
  margin-bottom: 4px;
  width: 260px;
  font-size: 13px;
  color: rgb(65, 65, 65);
  font-weight: 600;
  white-space: nowrap;
  overflow:hidden;
  text-overflow: ellipsis;
}
.imgBox {
  width: 285px;
  height: 300px;
  overflow: hidden;
}
span.원{
  color: black;
}
